function Business() {
    this.desc = '业务专属类-所有业务相关扩展方法均挂在在这个类身上！'
}
Business.prototype.extend = function (o) {
    if (Array.isArray(o)) {
        for (let i = 0; i < o.length; i++) {
            if (Object.prototype.toString.call(o[i]).slice(8, -1) === 'Object') {
                for (let k in o[i]) {
                    Business.prototype[k] = o[i][k]
                }
            }
        }
    } else if (Object.prototype.toString.call(o).slice(8, -1) === 'Object') {
        for (let k in o) {
            Business.prototype[k] = o[k]
        }
    }
}

export default new Business()
